<template>
  <div class="home" style="">
    <el-button type="info" round @click="start">Start</el-button>
  </div>
</template>

<script>
import axios from "axios"

export default {
  data() {
    return {
        userid: null
    }
  },
  methods: {
    async sendID(){
      await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ this.userid +'&num=0');
    },
    setIdRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const IDs = await axios.post("http://localhost:8040/setIDs", params)
      this.$_setuserID(IDs.data.userid)
    },
    start(){
      this.userid =  Math.random().toString(32).substring(2,12)
      this.sendID()
      this.setIdRequest(this.userid)
      this.$router.push('/quest').catch(()=>{});
    }
  }
}
</script>
